.title_wrapp{
    height: 100px;
    background-color: #fff;
    width: 500px;
    margin: auto;
    border-radius: 30px;
    margin-bottom: 70px;
    box-shadow: 10px 10px 11px -3px rgba(0, 0, 0, 0.75);
}
.title{
    font-size: 50px;
    display: flex;
    padding-top: 20px;
    justify-content: center;
    background-clip: text;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    background: -webkit-linear-gradient(90deg, #90cea1,#01b4e4);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}
.list_wrapp{
    padding-left: 20px;
    padding-right: 20px;
    display: flex;
    background-color: #fff;
    width: 1100px;
    height: 600px;
    margin: auto;
    border-radius: 30px;
    box-shadow: 10px 10px 11px -3px rgba(0, 0, 0, 0.75);
}

.not_found{
    color: #fff;
    font-size: 200px;
    display: flex;
    justify-content: center;
    align-items: center;
}